.Profile {
  padding: 0 50px;

  .main-selfie {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    .selfie {
      margin-top: 20px;
      width: 250px;
      height: 250px;
      border-radius: 50%;
      border: 5px solid black;
    }
  }

  .nav {
    .profile-menu {
      display: flex;
      justify-content: space-around;
      margin-top: 50px;
    }

    li {
      border: 1px solid #636e72;
      width: 50%;
      text-align: center;
      font-size: 20px;
      background-color: #b2bec3;
      transition: 0.5s;
      cursor: pointer;
    }
    .about-me {
      &:hover {
        color: white;
        background-color: black;
      }
    }

    .about-me-clicked {
      color: white;
      background-color: black;
    }

    .project-info {
      &:hover {
        color: white;
        background-color: black;
      }
    }

    .project-info-clicked {
      color: white;
      background-color: black;
    }
  }
}

.main-text {
  display: flex;
  justify-content: space-around;
  margin-top: 50px;
  font-size: 20px;
  text-align: center;

  .about-me-text {
    width: 50%;

    img {
      width: 50%;
    }
  }

  .about-me-hide {
    display: none;
  }

  .project-info-content {
    width: 50%;
  }

  .project-info-hide {
    display: none;
  }

  .content-box {
    .first-project {
      .first-project-content {
        display: flex;
        align-items: center;
        margin-top: 15px;
        border: 5px solid black;
        border-radius: 10px;

        .project-youtube {
          width: 400px;
          height: 250px;
          border-right: 5px solid black;
        }

        .icons {
          display: flex;
          flex-direction: column;
          margin-left: 20px;

          .github-info {
            display: flex;
            align-items: center;
            justify-content: space-around;

            .github-link {
              margin-left: 10px;
            }
          }

          img {
            width: 110px;
            height: 110px;
          }

          .velog-info {
            display: flex;
            align-items: center;
            justify-content: space-around;
            margin: 10px 10px 0 0;

            .velog {
              width: 110px;
              height: 110px;
              border-radius: 50%;
            }
          }
        }
      }
    }
    .second-project {
      margin-top: 20px;

      .second-project-content {
        display: flex;
        align-items: center;
        margin-top: 15px;
        border: 5px solid black;
        border-radius: 10px;

        .project-youtube {
          width: 400px;
          height: 250px;
          border-right: 5px solid black;
        }

        .icons {
          display: flex;
          flex-direction: column;
          margin-left: 20px;

          .github-info {
            display: flex;
            align-items: center;
            justify-content: space-around;

            .github-link {
              margin-left: 10px;
            }
          }

          img {
            width: 110px;
            height: 110px;
          }

          .velog-info {
            display: flex;
            align-items: center;
            justify-content: space-around;
            margin: 10px 10px 0 0;

            .velog {
              width: 110px;
              height: 110px;
              border-radius: 50%;
            }
          }
        }
      }
    }
  }
}
